import { Icon } from '@iconify/react';
import { Drawer, Portal, Tab, Tabs } from '@mui/material';
import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import MagneticButton from '../MagneticButton/MagneticButton';
import Stripe from '../Stripe/Stripe';
import './Navbar.css'

const Navbar = () => {
    const [show, setShow] = useState(false);
    const [expand, setExpand] = useState(false);

    const controlNavbar = () => {
        if (typeof window !== 'undefined') {
            setShow(window.scrollY > 300);
        }
    };

    useEffect(() => {
        if (typeof window !== 'undefined') {
            window.addEventListener('scroll', controlNavbar);

            return () => {
                window.removeEventListener('scroll', controlNavbar);
            };
        }
    }, [show]);


    return (
        <div className="navbar">
            <div className="title">
                <MagneticButton variant='text'><Link to='/'>Abner<b>Silva</b></Link></MagneticButton>
            </div>

            <div className="options">
                <MagneticButton disableRipple variant='text'><Link to='/works'>Trabalhos</Link></MagneticButton>
                <MagneticButton disableRipple variant='text'><Link to='/about'>Sobre</Link></MagneticButton>
                <MagneticButton disableRipple variant='text'><Link to='/contact'>Contato</Link></MagneticButton>
            </div>

            <Portal>
                <div className='btnController'
                    style={{
                        transform: `scale(${show ? '1' : '0'})`
                    }}
                >
                    <MagneticButton className={`scrolledNavBtn${expand ? ' active' : ''}`}
                        onClick={() => setExpand(!expand)}
                    >
                        <div className={`nav-icon${expand ? ' active' : ''}`}>
                            <span></span>
                            <span></span>
                            <span></span>
                            <span></span>
                        </div>
                    </MagneticButton>
                </div>
                <Drawer
                    anchor='right'
                    open={expand}
                    onClose={() => { setExpand(false) }}
                    PaperProps={{
                        style: {
                            width: 'calc(35% - 160px)',
                            display: 'flex',
                            justifyContent: 'center',
                            padding: '0 80px',
                            backgroundColor: '#1e1e1e',
                        }
                    }}
                    slotProps={{
                        backdrop: {
                            style: {
                                backdropFilter: 'blur(8px)'
                            }
                        }
                    }}
                >
                    <div className="title">NAVEGAO</div>
                    <Stripe />
                    <div className="options">
                        <MagneticButton variant='text'>Incio</MagneticButton>
                        <MagneticButton variant='text'>Trabalhos</MagneticButton>
                        <MagneticButton variant='text'>Sobre</MagneticButton>
                        <MagneticButton variant='text'>Contato</MagneticButton>
                    </div>

                    <div className="socials">
                        <div className="title">REDES SOCIAIS</div>
                        <div className="links">
                            <Link to={{ pathname: 'https://wa.me/5518988189353' }} target="_blank">
                                <MagneticButton variant='text'>
                                    <Icon icon="akar-icons:whatsapp-fill" />
                                </MagneticButton>
                            </Link>
                            <Link to={{ pathname: 'https://www.instagram.com/eae.abner/' }} target="_blank">
                                <MagneticButton variant='text'>
                                    <Icon icon="mdi:instagram" />
                                </MagneticButton>
                            </Link>
                            <Link to={{ pathname: 'https://www.linkedin.com/in/abner-j-silva/' }} target="_blank">
                                <MagneticButton variant='text'>
                                    <Icon icon="ri:linkedin-fill" />
                                </MagneticButton>
                            </Link>
                            <Link to={{ pathname: 'https://www.behance.net/abnerjsilva' }} target="_blank">
                                <MagneticButton variant='text'>
                                    <Icon icon="ri:behance-fill" />
                                </MagneticButton>
                            </Link>
                        </div>
                    </div>
                </Drawer>
            </Portal>
        </div>
    );
}

export default Navbar;