import React from 'react';
import './about.css';
import MagneticButton from 'src/components/MagneticButton/MagneticButton';
import { useParallax } from 'react-scroll-parallax';

const About = () => {
    const parallax = useParallax<HTMLDivElement>({
        easing: 'easeOutQuad',
        translateY: [50, -25],
    });
    const parallaxTxt = useParallax<HTMLDivElement>({
        easing: 'easeOutQuad',
        translateY: [50, -25],
    });
    const parallaxTxt2 = useParallax<HTMLDivElement>({
        easing: 'easeOutQuad',
        translateY: [50, -25],
    });


    return (
        <div className="about">
            <div className="paragraph" ref={parallaxTxt.ref}>
                Ol, me chamo Abner!
                <br />
                Desenvolvedor React pronto pra ajudar a projetar e/ou desenvolver o software que voc precisa.
                <br />
                Pronto para criar a sua experincia digital e do seu cliente!
            </div>
            <div className="about-btn" ref={parallaxTxt2.ref}>
                <div className="text">
                    A combinao da minha paixo por design, cdigo e interaes.
                </div>
                <div className="div" ref={parallax.ref}><MagneticButton>Sobre mim</MagneticButton></div>
            </div>
        </div>
    );
}

export default About;